import vueCity from './vueCity.vue';

const vueCitySelect= {
    install:function(Vue){
        Vue.component(vueCity.name,vueCity)
    }
}
// global  
if (typeof window !== 'undefined' && window.Vue) {
    window.Vue.use(vueCitySelect)
}
// 
export default vueCitySelect


